/**
* Media queries
**/
.featured-section {
  padding: 150px 0;
  background-color: var(--clr-grey);
}
@media only screen and (min-width: 1440px) {
  .featured-section {
    padding-top: 200px;
  }
}
@media only screen and (min-width: 1440px) {
  .featured-section {
    padding-top: 436px;
  }
}
.featured-section .textbox {
  text-align: center;
}
@media only screen and (min-width: 992px) {
  .featured-section .textbox {
    text-align: left;
    margin-top: 80px;
  }
}
.featured-section h1 {
  max-width: 500px;
  margin: 0 auto 20px;
}
@media only screen and (min-width: 992px) {
  .featured-section h1 {
    margin: 0 0 20px;
  }
}
@media only screen and (min-width: 1440px) {
  .featured-section h1 {
    max-width: none;
  }
}
.featured-section p {
  margin-bottom: 20px;
}
@media only screen and (min-width: 992px) {
  .featured-section p {
    max-width: 461px;
    margin-bottom: 64px;
  }
}
.featured-section a.button {
  margin: 0 auto 20px;
}
@media only screen and (min-width: 992px) {
  .featured-section a.button {
    margin-top: 64px;
    margin-left: 0;
  }
}
.featured-section__slider-wrapper {
  position: relative;
}
.featured-section__circle {
  position: absolute;
  cursor: pointer;
  z-index: 0;
}
@media only screen and (min-width: 992px) {
  .featured-section__circle--big {
    width: 529px;
    height: auto;
    bottom: 160px;
    left: 26px;
  }
}
@media only screen and (min-width: 992px) {
  .featured-section__circle--small {
    width: 330px;
    height: auto;
    bottom: 93px;
    right: 68px;
  }
}
.featured-section__slider {
  margin-top: 40px;
}
@media only screen and (min-width: 992px) {
  .featured-section__slider {
    margin-top: 0;
  }
}
.featured-section__slider .slide {
  position: relative;
}
.featured-section__slider .slide img {
  width: 100%;
  height: auto;
}